/*reset css start*/
blockquote, body, button, caption, dd, div, dl, dt, fieldset, figure, form, h1, h2, h3, h4, h5, h6, hr, html, input, legend, li, menu, ol, p, pre, table, td, textarea, th, ul {
  margin: 0;
  padding: 0;
  outline: 0;
}

address, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption, th {
  text-align: left;
}

abbr, body, fieldset, html, iframe, img {
  border: 0;
}

address, cite, dfn, em, i, var {
  font-style: normal;
}

[hidefocus], summary {
  outline: 0;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, small {
  font-size: 100%;
}

sub, sup {
  font-size: 83%;
}

code, kbd, pre, samp {
  font-family: inherit;
}

q:after, q:before {
  content: none;
}

textarea {
  overflow: auto;
  resize: none;
}

label, summary {
  cursor: default;
}

a, button {
  cursor: pointer;
}

a, a:hover, del, ins, s, u {
  text-decoration: none;
  outline: 0;
}

@font-face {
  font-family: "Axis";
  src: url("../font/Axis/ultralight.woff") format("woff");
}

body, button, input, keygen, legend, select, textarea {
  font-size: .75rem;
  line-height: 1.5;
  color: #333;
  outline: 0;
  font-family: Axis, KozGoPr6N, 'Heiti SC';
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

*, :after, :before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

input {
  box-shadow: none;
}

video {
  width: 100%;
  height: auto;
}

::-webkit-input-placeholder {
  color: #999;
}

:-moz-placeholder {
  color: #999;
}

::-moz-placeholder {
  color: #999;
}

:-ms-input-placeholder {
  color: #999;
}

/*reset css end*/
/*common css start*/
.hide {
  display: none;
}

.container {
  max-width: 1396px;
  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.scroll {
  transition: all ease-in-out 0.5s;
  transform: translate(0, 20px);
  opacity: 0;
}

.is_scoped {
  transform: translate(0, 0);
  opacity: 1;
}

.main-wrap {
  max-width: 1396px;
  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

/*common css end*/
/*common media start*/
/*common media end*/
header {
  position: relative;
  min-height: 200px;
}

header .header-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: .6666rem;
  line-height: .6666rem;
  background: transparent;
}

header .header-position {
  position: absolute;
  top: 1rem;
  width: 100%;
}

header .logo {
  width: 3.3333rem;
  height: 100%;
  background-image: url("../../image/Logo-fanohm-white.svg");
  background-size: 100%;
  background-repeat: no-repeat;
}

header .line-wraper {
  height: 20px;
  background-color: aqua;
}

header .nav > ul li {
  display: inline-block;
  margin-right: 1.24rem;
  font-size: .21333rem;
  color: black;
}

header .nav > ul li:nth-child(4) {
  display: inline-block;
  margin-right: 0;
}

header h1 {
  height: 34px;
  line-height: 34px;
  padding: 0 .22666rem;
  font-size: .18666rem;
  border-radius: 5px;
  color: #fff;
}

header .menu-wrap {
  display: flex;
  align-items: center;
  font-size: .18666rem;
  cursor: pointer;
}

header .menu-icon-wrap {
  position: relative;
  width: 16px;
  height: 14px;
  margin-left: 12px;
  transition: all 0.3s ease-in-out;
}

header .menu-icon-wrap span {
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #1a1a1a;
}

header .menu-icon-wrap span:nth-child(1) {
  top: 0;
}

header .menu-icon-wrap span:nth-child(2) {
  top: 6px;
}

header .menu-icon-wrap span:nth-child(3) {
  bottom: 0;
}

header .menu-icon-wrap.menu-icon-close {
  transform: rotate(360deg);
}

header .menu-icon-wrap.menu-icon-close span:nth-child(1) {
  transform: translateY(6px) rotate(-45deg);
}

header .menu-icon-wrap.menu-icon-close span:nth-child(2) {
  transform: translateY(0) rotate(45deg);
}

header .menu-icon-wrap.menu-icon-close span:nth-child(3) {
  opacity: 0;
}

footer {
  background-color: #111111;
}

footer .footer {
  position: relative;
  padding-top: .50666rem;
  padding-bottom: .50666rem;
  color: #fff;
  height: 5.454rem;
}

footer .copyright-wrap {
  position: absolute;
  bottom: .50666rem;
  color: #666;
  right: 15px;
}

@media screen and (max-width: 768px) {
  footer .copyright-wrap {
    position: relative;
    bottom: auto;
    right: auto;
  }
}
